/* Basic resets and font */
body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5; /* Default line height */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Base App Container */
.app-container {
  min-height: 100vh;
  background: linear-gradient(to bottom right, #4c51bf, #6b46c1); /* from-indigo-600 to-purple-800 */
  color: white;
  padding: 1rem; /* p-4 */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Header Styles */
.app-header {
  width: 100%;
  max-width: 32rem; /* max-w-lg */
  margin-bottom: 2rem; /* mb-8 */
  text-align: center;
}

.app-title {
  font-size: 2.25rem; /* text-4xl */
  font-weight: 800; /* font-extrabold */
  margin-bottom: 1rem; /* mb-4 */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2); /* text-shadow-lg */
}

.app-description {
  color: #a78bfa; /* text-indigo-200 */
  font-size: 0.875rem; /* text-sm */
}

.user-id-text {
  font-family: monospace; /* font-mono */
  font-size: 0.75rem; /* text-xs */
  word-break: break-all; /* break-all */
  background-color: rgba(99, 102, 241, 0.3); /* bg-indigo-500 bg-opacity-30 */
  border-radius: 0.25rem; /* rounded */
  padding: 0 0.25rem; /* px-1 */
}


/* Card Container Styles */
.card-container {
  width: 100%;
  max-width: 28rem; /* max-w-md */
  background-color: white;
  padding: 1.5rem; /* p-6 */
  border-radius: 0.75rem; /* rounded-xl */
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04); /* shadow-2xl */
  display: flex;
  flex-direction: column;
  gap: 1.5rem; /* space-y-6 */
  margin-bottom: 2rem; /* mb-8 */
  position: relative; /* for absolute positioning of suggestions */
}

/* Input and Button Group */
.input-button-group {
  display: flex;
  flex-direction: column;
  gap: 1rem; /* gap-4 */
  position: relative; /* for absolute positioning of suggestions */
}

/* Responsive adjustment for input/button group */
@media (min-width: 640px) { /* sm breakpoint */
  .input-button-group {
    flex-direction: row;
  }
}

/* Input Field Styles */
.text-input {
  flex-grow: 1; /* flex-grow */
  padding: 0.75rem; /* p-3 */
  border-radius: 0.5rem; /* rounded-lg */
  border: 1px solid #d1d5db; /* border border-gray-300 */
  outline: none; /* Remove default outline */
  font-size: 1.125rem; /* text-lg */
  color: #1f2937; /* text-gray-800 */
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05); /* shadow-sm */
  transition: border-color 0.2s, box-shadow 0.2s; /* Add transition for focus */
}

.text-input:focus {
  border-color: transparent; /* focus:border-transparent */
  box-shadow: 0 0 0 2px #6366f1; /* focus:ring-2 focus:ring-indigo-500 */
}

/* Button Styles */
.action-button {
  background-color: #4338ca; /* bg-indigo-700 */
  color: white;
  font-weight: 700; /* font-bold */
  padding: 0.75rem 1.5rem; /* py-3 px-6 */
  border-radius: 0.5rem; /* rounded-lg */
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); /* shadow-lg */
  transition: all 0.3s ease-in-out; /* transform transition duration-300 ease-in-out */
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem; /* gap-2 */
}

.action-button:hover {
  background-color: #3730a3; /* hover:bg-indigo-800 */
}

.action-button:active {
  transform: scale(0.95); /* active:scale-95 */
}

.action-button:focus {
  outline: none; /* focus:outline-none */
  box-shadow: 0 0 0 2px #6366f1, 0 0 0 4px #e0e7ff; /* focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 */
}

.action-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Green Add to Portfolio Button */
.add-portfolio-button {
  background-color: #22c55e; /* bg-green-600 */
}

.add-portfolio-button:hover {
  background-color: #16a34a; /* hover:bg-green-700 */
}

.add-portfolio-button:focus {
  box-shadow: 0 0 0 2px #22c55e, 0 0 0 4px #bbf7d0; /* focus:ring-2 focus:ring-green-500 focus:ring-offset-2 */
}

/* Red Remove Button */
.remove-button {
  background-color: #ef4444; /* bg-red-500 */
  padding: 0.5rem 1rem; /* py-2 px-4 */
  font-size: 0.875rem; /* text-sm */
}

.remove-button:hover {
  background-color: #dc2626; /* hover:bg-red-600 */
}

.remove-button:focus {
  box-shadow: 0 0 0 2px #ef4444, 0 0 0 4px #fca5a5; /* focus:ring-2 focus:ring-red-400 focus:ring-offset-2 */
}

/* Loading Spinner */
.spinner {
  animation: spin 1s linear infinite;
  height: 1.25rem; /* h-5 */
  width: 1.25rem; /* w-5 */
  color: white; /* text-white */
  display: inline-block;
  margin-right: 0.5rem; /* mr-2 */
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Error Message Styles */
.error-message {
  color: #dc2626; /* text-red-500 */
  background-color: #fee2e2; /* bg-red-100 */
  padding: 0.75rem; /* p-3 */
  border-radius: 0.5rem; /* rounded-lg */
  border: 1px solid #fecaca; /* border border-red-200 */
  text-align: center;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06); /* shadow-md */
}

/* Stock Data Display Card */
.stock-data-card {
  background: linear-gradient(to right, #f9fafb, #f3f4f6); /* bg-gradient-to-r from-gray-50 to-gray-100 */
  padding: 1.5rem; /* p-6 */
  border-radius: 0.75rem; /* rounded-xl */
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); /* shadow-lg */
  border: 1px solid #e5e7eb; /* border border-gray-200 */
  color: #1f2937; /* text-gray-800 */
  display: flex;
  flex-direction: column;
  gap: 1rem; /* space-y-4 */
}

.stock-data-card h2 {
  font-size: 1.875rem; /* text-3xl */
  font-weight: 700; /* font-bold */
  color: #4338ca; /* text-indigo-700 */
  margin-bottom: 0.5rem; /* mb-2 */
}

.stock-data-card p {
  font-size: 1.125rem; /* text-lg */
}

.stock-data-card strong {
  font-weight: 700; /* font-bold */
}

.stock-price {
  font-size: 1.5rem; /* text-2xl */
  font-weight: 800; /* font-extrabold */
}

.stock-price .value {
  color: #10b981; /* text-green-600 */
}

.stock-change {
  font-size: 1.25rem; /* text-xl */
  font-weight: 600; /* font-semibold */
}

.stock-change.positive {
  color: #22c55e; /* text-green-500 */
}

.stock-change.negative {
  color: #ef4444; /* text-red-500 */
}

.stock-details-grid {
  display: grid;
  grid-template-columns: 1fr; /* grid-cols-1 */
  gap: 0.75rem; /* gap-3 */
  font-size: 1.125rem; /* text-lg */
}

@media (min-width: 640px) { /* sm breakpoint */
  .stock-details-grid {
    grid-template-columns: repeat(2, 1fr); /* sm:grid-cols-2 */
  }
}

.data-note {
  font-size: 0.875rem; /* text-sm */
  color: #4b5563; /* text-gray-600 */
  font-style: italic; /* italic */
  margin-top: 1rem; /* mt-4 */
}

/* Suggestions List */
.suggestions-list {
  position: absolute;
  z-index: 10;
  left: 0;
  right: 0;
  width: 100%;
  background-color: white;
  border: 1px solid #d1d5db; /* border border-gray-300 */
  border-radius: 0.5rem; /* rounded-lg */
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); /* shadow-lg */
  top: calc(100% + 0.5rem); /* top-[calc(100%+0.5rem)] */
  max-height: 15rem; /* max-h-60 */
  overflow-y: auto;
  color: #1f2937; /* text-gray-800 */
}

.suggestions-list li {
  padding: 0.75rem; /* p-3 */
  cursor: pointer;
  border-bottom: 1px solid #f3f4f6; /* border-b border-gray-100 */
  transition: background-color 0.15s ease-in-out;
}

.suggestions-list li:last-child {
  border-bottom: none; /* last:border-b-0 */
}

.suggestions-list li:hover {
  background-color: #eef2ff; /* hover:bg-indigo-50 */
}

.suggestions-list li span {
  font-weight: 600; /* font-semibold */
}

/* Portfolio Section */
.portfolio-section {
  width: 100%;
  max-width: 28rem; /* max-w-md */
  background-color: white;
  padding: 1.5rem; /* p-6 */
  border-radius: 0.75rem; /* rounded-xl */
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04); /* shadow-2xl */
  display: flex;
  flex-direction: column;
  gap: 1rem; /* space-y-4 */
  margin-top: 2rem; /* mt-8 */
}

.portfolio-section h2 {
  font-size: 1.875rem; /* text-3xl */
  font-weight: 700; /* font-bold */
  color: #4338ca; /* text-indigo-700 */
  margin-bottom: 1rem; /* mb-4 */
  text-align: center;
}

.portfolio-grid {
  display: grid;
  grid-template-columns: 1fr; /* grid-cols-1 */
  gap: 1rem; /* gap-4 */
}

.portfolio-item {
  background-color: #f9fafb; /* bg-gray-50 */
  padding: 1rem; /* p-4 */
  border-radius: 0.5rem; /* rounded-lg */
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06); /* shadow-md */
  border: 1px solid #e5e7eb; /* border border-gray-200 */
  color: #1f2937; /* text-gray-800 */
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: grab; /* cursor-grab for draggable items */
}

.portfolio-item.dragging {
  opacity: 0.5; /* opacity-50 */
  border: 2px solid #3b82f6; /* border-blue-500 border-2 */
}

.portfolio-item h3 {
  font-size: 1.25rem; /* text-xl */
  font-weight: 700; /* font-bold */
  color: #4f46e5; /* text-indigo-600 */
}

.portfolio-item .company-name {
  font-size: 0.875rem; /* text-sm */
  color: #4b5563; /* text-gray-600 */
}

.portfolio-item .price {
  font-size: 1.125rem; /* text-lg */
  font-weight: 600; /* font-semibold */
  margin-top: 0.25rem; /* mt-1 */
}

.portfolio-item .price .value {
  color: #10b981; /* text-green-600 */
}

.portfolio-item .change-text {
  font-size: 1rem; /* text-md */
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5); /* bg-black bg-opacity-50 */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
  padding: 1rem; /* p-4 */
}

.modal-content {
  background-color: white;
  padding: 1.5rem; /* p-6 */
  border-radius: 0.5rem; /* rounded-lg */
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04); /* shadow-xl */
  color: #1f2937; /* text-gray-800 */
  width: 100%;
  max-width: 24rem; /* max-w-sm */
  display: flex;
  flex-direction: column;
  gap: 1rem; /* space-y-4 */
}

.modal-content h3 {
  font-size: 1.25rem; /* text-xl */
  font-weight: 700; /* font-bold */
  color: #4338ca; /* text-indigo-700 */
}

.modal-input {
  width: 100%;
  padding: 0.75rem; /* p-3 */
  border-radius: 0.5rem; /* rounded-lg */
  border: 1px solid #d1d5db; /* border border-gray-300 */
  outline: none;
  font-size: 1.125rem; /* text-lg */
  color: #1f2937;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  transition: border-color 0.2s, box-shadow 0.2s;
}

.modal-input:focus {
  border-color: transparent;
  box-shadow: 0 0 0 2px #6366f1;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem; /* gap-3 */
}

.modal-cancel-button {
  background-color: #d1d5db; /* bg-gray-300 */
  color: #1f2937; /* text-gray-800 */
}

.modal-cancel-button:hover {
  background-color: #9ca3af; /* hover:bg-gray-400 */
}

.modal-create-button {
  background-color: #4f46e5; /* bg-indigo-600 */
  color: white;
}

.modal-create-button:hover {
  background-color: #4338ca; /* hover:bg-indigo-700 */
}

